.o-MobileBar {
  justify-content: flex-end;
}
.o-MobileBar__Item {
  flex: none;
  padding: 0.75rem 1rem;
  padding: calc(0.5 * var(--lh)) 1rem;
  padding: 0.5lh 1rem;
}
.o-MobileBar__Item,
.o-MobileBar__Item:hover,
.o-MobileBar__Item:focus {
  color: inherit;
}
.o-MobileBar__Item .glyphicons {
  line-height: inherit;
}
